/* hide header */
let scrollWidthFunc = () => {
    let scrollWidth = window.innerWidth - document.body.clientWidth;
    document.querySelector('html').style.paddingRight = scrollWidth + 'px';
    document.querySelector('header').style.paddingRight = scrollWidth + 'px';
}
const scrollTop = document.querySelector('.scroll-top');
if (scrollTop)
    scrollTop.addEventListener('click', () => {
        window.scrollTo({ top: 0, behavior: 'smooth' });
    });



['load', 'resize'].forEach((event) => {
    window.addEventListener(event, function () {
        let headerHeight = header.clientHeight;
        const headerTop = header.querySelector('.header__top');
        if (headerTop) {
            var originalHeightHeaderTop = headerTop.offsetHeight;
        }
        window.onscroll = function (e) {
            if (window.scrollY > headerHeight) {
                if (!headerTop.classList.contains('hide')) {
                    if(window.innerWidth > 1200) {
                        headerTop.classList.add('hide');
                        headerTop.style.height = '0px';
                        headerTop.style.opacity = '0';
                        headerTop.style.overflow = 'hidden';
                    }
                }
            }
            else {
                headerTop.style.height = originalHeightHeaderTop + 'px';
                headerTop.classList.remove('hide');
                headerTop.style.opacity = '1';
                headerTop.style.overflow = 'visible';
            }
        };
    })
})
/* hide header */


document.addEventListener("DOMContentLoaded", function () {

    /* open submenu */
    const headerNavBtn = document.querySelector('.header__nav_services');
    if(headerNavBtn) {
        headerNavBtn.addEventListener('click', (e) => {
            document.querySelector('html').addEventListener('click', function (e) {
                if (!e.target.closest('.header__nav_services')) {
                    headerNavBtn.classList.remove('active');
                    return;
                }
            })
            headerNavBtn.classList.toggle('active');
        })
    }
    /* end open submenu */


    /* burger menu */
    const burgerMenus = document.querySelectorAll('.burger__menu');
    burgerMenus.forEach(burgerMenu => {
        if (burgerMenu) {
            const headerMobile = document.querySelector('.header__menu');
            const header = document.querySelector('.header');
            burgerMenu.addEventListener("click", () => {
                headerMobile.classList.toggle("header__menu--active");
                burgerMenu.classList.toggle("burger__menu--active");
                document.body.classList.toggle('burger-lock');
                document.querySelector('html').classList.toggle('burger-lock');
            });
        }
    })
    /* end burger menu */


    /* Функция переключения табов */
    const showTab = (elTabBtn) => {
        const elTab = elTabBtn.closest('.tab');
        if (!elTab || elTabBtn.classList.contains('active')) return;
        const targetId = elTabBtn.dataset.id;

        elTab.querySelectorAll('.tab-btn').forEach(btn => btn.classList.remove('active'));
        elTab.querySelectorAll('.tab-content').forEach(content => content.classList.remove('active'));

        elTabBtn.classList.add('active');
        elTab.querySelector(`.tab-content[data-id="${targetId}"]`)?.classList.add('active');

        const accordionContent = elTab.closest('.accordion__content');
        if (accordionContent) {
            updateAccordionHeight(accordionContent);
        }
    };

    /* Вешаем обработчики на все кнопки табов */
    document.querySelectorAll('.tab-btn').forEach(btn => {
        if(btn) {
            btn.addEventListener('click', function () {
                showTab(this);
            });
        }
    });


    /* Функция обновления высоты аккордеона */
    const updateAccordionHeight = (accordionContent) => {
        if (!accordionContent) return;
        const currentHeight = accordionContent.offsetHeight;
        accordionContent.style.maxHeight = 'none';
        const newHeight = accordionContent.scrollHeight;
        accordionContent.style.maxHeight = `${currentHeight}px`;
        setTimeout(() => {
            accordionContent.style.maxHeight = `${newHeight}px`;
        }, 10);
    };

    /* Функция переключения аккордеонов */
    const accordions = document.querySelectorAll('.accordion');
    if(accordions) {
        accordions.forEach(acc => {
            acc.addEventListener('click', function () {
                const accContent = this.querySelector('.accordion__content')  || this.parentElement.querySelector('.accordion__content');
        
                if (accContent.classList.contains('accordion__content--active')) {
                    accContent.classList.remove('accordion__content--active');
                    this.classList.remove('accordion--active');
                    accContent.style.maxHeight = '0';
                } else {
                    accContent.classList.add('accordion__content--active');
                    this.classList.add('accordion--active');
                    
                    updateAccordionHeight(accContent);
                }
            });
        });
    }



    /* Изменение высоты контента на стадиях */
    function editStagesHeight() {
        const stagesBtns = document.querySelector('.stages__btns');
        const stagesСontents = document.querySelectorAll('.stages__content_wrap');
        if(stagesBtns && stagesСontents) {
            stagesСontents.forEach(stagesСontent => {
                stagesСontent.style.maxHeight = stagesBtns.scrollHeight + "px"; 
            });
        }
    };
    editStagesHeight();

    /*  btn more  */
    function editMoreContentHeight() {
        const moreBtns = document.querySelectorAll('.btn-more');
        moreBtns.forEach(moreBtn => {
            if (moreBtn) {
                const moreContent = moreBtn.previousElementSibling;
                moreBtn.style.display = "";
                moreBtn.classList.remove('show');
                moreContent.classList.remove('show');
                editStagesHeight();

                moreContent.closest(".tab-content") ? moreContent.closest(".tab-content").style.display = "block" : "";
                const contentScrollHeight = moreContent.scrollHeight;
                const contentClientHeight = moreContent.clientHeight;
                moreContent.closest(".tab-content") ? moreContent.closest(".tab-content").style.display = "" : "";

                if (contentScrollHeight <= contentClientHeight) {
                    moreBtn.style.display = 'none'; 
                    moreContent.classList.add('show');
                } else {
                    const textBtn = moreBtn.innerHTML; 
                    moreBtn.addEventListener('click', function() {
                        if (!moreBtn.classList.contains('show')) {
                            moreContent.style.maxHeight = moreContent.scrollHeight + "px"; 
                            this.textContent = 'Свернуть';
                            this.classList.add('show');
                            moreContent.classList.add('show');
                        } else {
                            this.classList.remove('show');
                            moreContent.classList.remove('show');
                            moreContent.style.maxHeight = contentClientHeight + "px"; 
                            this.textContent = textBtn;
                            if(moreContent.classList.contains("stages__content_wrap")) {
                                editStagesHeight() ;
                            }
                        }
                    });
                }
            }
        });
    }
    editMoreContentHeight();


    // add footer nav accordion
    window.addEventListener('resize', () => {
        const footerNavAcc = document.querySelector('.footer__nav-servisces ul');
        if(footerNavAcc) {
            if(window.innerWidth <= 768) {
                footerNavAcc.classList.add('accordion__content');
            } else {
                footerNavAcc.classList.remove('accordion__content');
            }
        }
    });


    // Popups
    function popupClose(popupActive) {
        popupActive.classList.remove('open');
        setTimeout(() => {
            if (!popupActive.classList.contains('open')) {
                popupActive.classList.remove('active');
            }
        }, 400);
        document.body.classList.remove('lock');
        document.querySelector('html').style.paddingRight = 0;
        document.querySelector('html').classList.remove('lock');
        document.querySelector('header').removeAttribute('style');


    }
    const popupOpenBtns = document.querySelectorAll('.popup-btn');
    const popups = document.querySelectorAll('.popup');
    const originalTitlePopup2 = document.querySelector('.original-title').innerHTML;
    const closePopupBtns = document.querySelectorAll('.close-popup-btn');
    closePopupBtns.forEach(function (el) {
        el.addEventListener('click', function (e) {
            popupClose(e.target.closest('.popup'));
        });
    });
    popupOpenBtns.forEach(function (el) {
        el.addEventListener('click', function (e) {
            e.preventDefault();
            const path = e.currentTarget.dataset.path;
            const currentPopup = document.querySelector(`[data-target="${path}"]`);
            if (currentPopup) {
                popups.forEach(function (popup) {
                    popupClose(popup);
                    popup.addEventListener('click', function (e) {
                        if (!e.target.closest('.popup__content')) {
                            popupClose(e.target.closest('.popup'));
                        }
                    });
                });
                currentPopup.classList.add('active');
                setTimeout(() => {
                    currentPopup.classList.add('open');
                }, 10);
                if (currentPopup.getAttribute('data-target') == 'popup-change') {

                    let originaTitle = currentPopup.querySelector('.original-title');
                    if (el.classList.contains('change-item__btn')) {

                        if (el.classList.contains('doctor__btn-js')) {
                            let currentItem = el.closest('.change-item');
                            let currentTitile = currentItem.querySelector('.change-item__title');
                            originaTitle.innerHTML = 'Записаться на приём к врачу: ' + currentTitile.innerHTML
                        }
                        else {
                            if (el.classList.contains('change-item__btn_current')) {
                                originaTitle.textContent = el.textContent;
                            }
                            else {
                                let currentItem = el.closest('.change-item');
                                let currentTitile = currentItem.querySelector('.change-item__title');
                                originaTitle.innerHTML = currentTitile.innerHTML
                            }
                        }
                    }
                    else {
                        originaTitle.innerHTML = originalTitlePopup2;
                    }
                }

                if (currentPopup.getAttribute('data-target') == 'popup-jobs') {
                    let currentItems = el.closest('.jobs__items') 
                    let originalText = currentPopup.querySelector('.jobs__inner_original');
                    if(originalText && currentItems.querySelector('.jobs__inner')) {
                        originalText.innerHTML = currentItems.querySelector('.jobs__inner').innerHTML;
                    }
                }
                e.stopPropagation();
                scrollWidthFunc();
                document.querySelector('html').classList.add('lock');
            }
        });
    });
    // end popups


    /* yandex map */
    const mapPlaceholder = document.getElementById('map-placeholder');
    if (mapPlaceholder) {
        mapPlaceholder.addEventListener('mouseenter', loadMap, { once: true });
        mapPlaceholder.addEventListener('click', loadMap, { once: true });
    }

    function loadMap() {
        if (!document.querySelector('[src="https://api-maps.yandex.ru/2.1/?lang=ru_RU"]')) {
            const script = document.createElement('script');
            script.type = 'text/javascript';
            script.src = 'https://api-maps.yandex.ru/2.1/?lang=ru_RU';
            script.onload = initMap;
            document.head.appendChild(script);
        } else {
            initMap();
        }
    }
    
    function initMap() {
        const mapPlaceholder = document.getElementById('map-placeholder');
        if (mapPlaceholder) {
            mapPlaceholder.remove();
        }
    
        ymaps.ready(function () {
            const myMap = new ymaps.Map('map', {
                center: [47.231129, 39.728721],
                zoom: 14,
                controls: []
            });
    
            const myPlacemark = new ymaps.Placemark(
                [47.231129, 39.728721],
                {
                    hintContent: 'Клиника ТорпедоМед',
                    balloonContent: 'Клиника ТорпедоМед'
                },
                {
                    iconLayout: 'default#image',
                    iconImageHref: 'assets/img/icons/map-pin.png',  //заменить на свою иконку
                    iconImageSize: [40, 50],
                    iconImageOffset: [-20, -30],
                }
            );
    
            myMap.geoObjects.add(myPlacemark);
            myMap.behaviors.disable(['scrollZoom']);
        });
    }
    /* end yandex map */


    /* filter element by select */
    const filterBlocks = document.querySelectorAll('.filter-block');

    filterBlocks.forEach(block => {
        const selects = block.querySelectorAll('.filter-select');
        const cards = block.querySelectorAll('.filter-card');
        const quantity = block.querySelector('.quantity span');

        function filterCards() {
            let visibleCount = 0;

            cards.forEach(card => {
                let isVisible = true;
                selects.forEach(select => {
                    const filterType = select.getAttribute('data-filter-type');
                    const selectedValue = select.value;
                    const cardValue = card.getAttribute(`data-${filterType}`);
                    if (selectedValue !== 'all' && selectedValue !== cardValue) {
                        isVisible = false;
                    }
                });
                card.style.display = isVisible ? '' : 'none';
                if (isVisible) visibleCount++;
            });
            if(quantity) {
                quantityElem();
            }
        }
        selects.forEach(select => {
            select.addEventListener('change', filterCards);
        });
        filterCards();
    });
    /* end filter element by select */


    // quantity-card
    function quantityElem() {
        const quantityCards = document.querySelectorAll('.quantity-card');
        const quantityElement = document.querySelector('.quantity span');
        if (quantityElement) {
            let visibleCards = 0;
            quantityCards.forEach(card => {
                if (card.offsetParent !== null) {
                    visibleCards++;
                }
            });
            quantityElement.textContent = visibleCards;
        }
    }
    setTimeout(quantityElem, 100);



    // open widget 
    const widgetBtn = document.querySelector('.popup-widget__plus');
    const widget = document.querySelector('.popup-widget');

    if (widgetBtn && widget) {
        widgetBtn.addEventListener('click', (event) => {
            widget.classList.toggle("open");
            event.stopPropagation(); 
        });

        document.addEventListener('click', (event) => {
            if(widget.classList.contains("open")) {
                if (!widget.contains(event.target)) {
                    widgetBtn.click(); 
                }
            }
        });
    }



    /* show more gallery cards */
    const galleryCards = document.querySelectorAll(".gallery__cards a");
    const showMoreBtn = document.querySelector(".show-more");
    if(galleryCards && showMoreBtn) { 
        let quantity = 8;
        let step = 4; 
        if(window.innerWidth < 1200) {
            quantity = 6;
            step = 3; 
        }
        let visibleCount = quantity;

        galleryCards.forEach((card, index) => {
            if (index >= quantity) {
                card.style.display = "none";
            }
        });
    
        showMoreBtn.addEventListener("click", function () {
            for (let i = visibleCount; i < visibleCount + step; i++) {
                if (galleryCards[i]) {
                    galleryCards[i].style.display = "block";
                }
            }
            visibleCount += step;
            if (visibleCount >= galleryCards.length) {
                showMoreBtn.style.display = "none";
            }
        });

        if (galleryCards.length <= step) {
            showMoreBtn.style.display = "none";
        }
    }
    /* end show more gallery cards */





    /* pagination for test */
    const contentTest = document.querySelector('.test__list'); 
    const itemsPerPage = 1; // set number of items per page
    let currentPage = 0;

    if (contentTest && contentTest.getElementsByClassName('test__item')) {
        const itemsTest = Array.from(contentTest.getElementsByClassName('test__item')).slice(0);

        function showPage(page) {
            const startIndex = page * itemsPerPage;
            const endIndex = startIndex + itemsPerPage;
            itemsTest.forEach((item, index) => {
                item.classList.toggle('hidden', index < startIndex || index >= endIndex);
            });
            updatePaginationState();
            updateBottomButtons();
        }

        function createPagination() {
            const totalPages = Math.ceil(itemsTest.length / itemsPerPage);
            const paginationContainer = document.querySelector('.pagination');
            paginationContainer.innerHTML = '';
            
            for (let i = 0; i < totalPages; i++) {
                const pageIndicator = document.createElement('span');
                //pageIndicator.textContent = i + 1;
                pageIndicator.classList.add('pagination__item');
                paginationContainer.appendChild(pageIndicator);
            }
        }

        function updatePaginationState() {
            const pageIndicators = document.querySelectorAll('.pagination__item');
            pageIndicators.forEach((indicator, index) => {
                indicator.classList.remove('active', 'completed');
                if (index < currentPage) {
                    indicator.classList.add('completed');
                } else if (index === currentPage) {
                    indicator.classList.add('active');
                }
            });
        }

        function updateBottomButtons() {
            const totalPages = Math.ceil(itemsTest.length / itemsPerPage);
            const nextButton = document.querySelector('.test__btn_next');
            const finishButton = document.querySelector('.test__btn_finish');
            
            if (currentPage === totalPages - 1) {
                nextButton.style.display = "none";
                finishButton.style.display = "block";
            } else {
                nextButton.style.display = "block";
                finishButton.style.display = "none";
            }
        }

        const nextButton = document.querySelector('.test__btn_next');
        if (nextButton) {
            nextButton.addEventListener('click', () => {
                const totalPages = Math.ceil(itemsTest.length / itemsPerPage);
                if (currentPage < totalPages - 1) {
                    currentPage++;
                    showPage(currentPage);
                }
            });
        }

        createPagination(); 
        showPage(currentPage);
    }
    /* end pagination for test */
















 


    /*  search */
	const inputSearch = document.querySelectorAll('input[type=search]')
	if (inputSearch.length > 0) {
		inputSearch.forEach(elem => {
			const wrapper = elem.closest('.search-wrapper')
			if (wrapper) {
				const searchResultBlock = wrapper.querySelector('.popup__search-result')
				const popularCitiesBlock = wrapper.querySelector('.popup__search')
				const noResultsMessage = searchResultBlock.querySelector('.no-results-message')

				function search() {
					let filter = elem.value.toUpperCase()
					let ul = wrapper.querySelectorAll('.search-list')
					let totalResults = 0

					ul.forEach(item => {
						let li = item.getElementsByTagName('li')
						for (let i = 0; i < li.length; i++) {
							let a = li[i].querySelector('.search-name')
							if (a.innerHTML.toUpperCase().indexOf(filter) > -1) {
								li[i].classList.remove('none')
								totalResults++
							} else {
								li[i].classList.add('none')
							}
						}
					})
                    noResultsMessage.classList.toggle('none', totalResults > 0);

					if (elem.value.trim() === '') {
						searchResultBlock.classList.add('none')
						popularCitiesBlock.classList.remove('none')
					} else {
						searchResultBlock.classList.remove('none')
						popularCitiesBlock.classList.add('none')
					}
				}
				elem.addEventListener('input', search)

                document.addEventListener('click', (event) => {
                    if (!wrapper.contains(event.target)) {
                        searchResultBlock.classList.add('none')
                    }
                })
			}
		})
	}
    /*  end search  */  


    /* search in page */
    const searchInput = document.getElementById('searchInput')
    if (searchInput) {
        searchInput.addEventListener('input', function () {
            const searchValue = this.value.toLowerCase();
            const rows = document.querySelectorAll('.search-page-item'); 
        
            rows.forEach(function (row) {
                const serviceName = row.querySelector('.search-page-name').textContent.toLowerCase();
        
                if (serviceName.includes(searchValue)) {
                    row.style.display = '';
                } else {
                    row.style.display = 'none';
                }
            });
            quantityElem();
            checkAndHideBlocksIfEmpty();
        });
    }


    /* footer Nav Accordeon */
    const footerNavAccordeon = document.querySelectorAll('.footer__nav_acc');
    footerNavAccordeon.forEach(footerBtn => {
        if (footerBtn) {
            footerBtn.addEventListener('click', function() {
                const footerContent = footerBtn.nextElementSibling;
                footerContent.classList.toggle('active');
                this.classList.toggle('active');
            });
        }
    });

    /*   scrollTop  */
	const buttonsUp = document.querySelectorAll('.is-scroll-up')
    buttonsUp.forEach(buttonUp => {
        if (buttonUp) {
            buttonUp.addEventListener('click', function () {
                window.scrollTo({
                    top: 0,
                    behavior: 'smooth',
                })
            })
        }
    });
    /*   end scrollTop  */

})


/* navigation */
const articleNavigation = document.querySelector(".navigation");
if (articleNavigation) {
    const jsScrollBlockList = document.querySelectorAll(
        ".text h1, .text h2, .text h3, .text h4, .text h5"
    );

    if (jsScrollBlockList.length > 0) {
        const articleNavigationList = document.querySelector(".navigation__list");
        let currentSublist = null;
        let lastH2Item = null;

        jsScrollBlockList.forEach((jsScrollBlock, i) => {
            const titleBlock = jsScrollBlock.textContent;
            const articleNavigationItem = document.createElement("li");
            const articleNavigationLink = document.createElement("a");

            articleNavigationItem.classList.add("navigation__list_item");
            articleNavigationItem.classList.add("accordion");
            articleNavigationLink.classList.add("navigation__list_link");
            jsScrollBlock.setAttribute("id", `heading-${i}`);
            articleNavigationLink.setAttribute("href", `#heading-${i}`);
            articleNavigationLink.textContent = titleBlock;
            articleNavigationItem.appendChild(articleNavigationLink);

            if (jsScrollBlock.tagName === "H2") {
                articleNavigationItem.classList.add("nav-title-h2");
                articleNavigationList.appendChild(articleNavigationItem);
                currentSublist = document.createElement("ul");
                currentSublist.classList.add("navigation__sublist");
                currentSublist.classList.add("accordion__content");
                articleNavigationItem.appendChild(currentSublist);
                lastH2Item = articleNavigationItem;
            } else if (lastH2Item) {
                articleNavigationItem.classList.add(`nav-title-${jsScrollBlock.tagName.toLowerCase()}`);
                currentSublist.appendChild(articleNavigationItem);
            }
        });

        document.querySelectorAll('a[href^="#"]').forEach((link) => {
            link.addEventListener("click", function (e) {
                e.preventDefault();
                const targetId = this.getAttribute("href").substring(1);
                const scrollTarget = document.getElementById(targetId);
                if (scrollTarget) {
                    const topOffset = 150;
                    const elementPosition = scrollTarget.getBoundingClientRect().top;
                    const offsetPosition = elementPosition - topOffset;
                    window.scrollBy({
                        top: offsetPosition,
                        behavior: "smooth",
                    });
                }
            });
        });
    } else {
        const existingNavList = articleNavigation.querySelector(".navigation__list");
        if (existingNavList) {
            existingNavList.remove();
        }
    }
}

/* end navigation */